<template>
    <div>
        <div class="comment">
            <h2></h2>
            <div class="comment-msg">
                <div class="comment-img">
                    <img :src="attachImageUrl(avator)">
                </div>
                <el-input class="comment-input" type="textarea" :rows="2" placeholder="" v-model="textarea">
                </el-input>
            </div>
            <el-button type="primary" class="sub-btn" @click="postComment"></el-button>
        </div>
        <p>{{this.commentList.length}}</p>
        <hr/>
        <ul class="popular" v-for="(item,index) in data" :key="index">
            <li>
                <div class="popular-img">
                    <img :src="attachImageUrl(getUserByCommentUserId(item.userId).avator)">
                </div>
                <div class="popular-msg">
                    <ul>
                        <li class="name">{{ getUserByCommentUserId(item.userId).username }}</li>
                        <li class="time">{{ item.commentTime }}</li>
                        <li class="content">{{ item.content }}</li>
                    </ul>
                </div>
                <div class="up" ref="up" @click="postUp(item.id,item.up,index)">
                    <svg class="icon">
                        <use xlink:href="#icon-zan"></use>
                    </svg>
                    {{ item.up }}
                </div>
            </li>
        </ul>
        <div class="pagination">
                <el-pagination @current-change="handleCurrentChange" background layout="total,prev,pager,next"
                    :current-page="currentPage" :page-size="pageSize" :total="commentList.length"></el-pagination>
            </div>
    </div>
</template>

<script>
import { mapGetters } from 'vuex';
import { addComment,getAllComment, getUserOfId,addLike } from '../api';
import {mixin} from '../mixins'
export default{
    name:'comment',
    mixins:[mixin],
    props:[
        'playId',       //id
        'type',         //01
    ],
    computed:{
        ...mapGetters([
            'loginIn',      //
            'userId',       //id
            'id',           //id
            'avator',       //
        ]),
        data(){
            return this.commentList.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize)
        }
    },
    data(){
        return{
            textarea:'',        //
            commentList:[],     //
            commentUser:[],     //
            pageSize:15,        //15
            currentPage:1,      //
        }
    },
    mounted(){
        this.getComment()
    },
    methods:{
        //id
        getUserByCommentUserId(id){
            for(let item of this.commentUser){
                if(item.id==id){
                    return item
                }
            }
        },
        //
        postComment(){
            if(this.loginIn){
                let params=new URLSearchParams()
                if(this.type==0){   //type,0,1
                    params.append('bookId',this.playId)
                }else{
                    params.append('bookListId',this.playId)
                }
                params.append('userId',this.userId)
                params.append('type',this.type)
                params.append('content',this.textarea)
                addComment(params)
                .then(res=>{
                    if(res.code==1){
                        this.notify('','success')
                        this.textarea=''
                        this.commentUser=[]
                        this.getComment()
                        //this.$router.go(0);
                    }else{
                        this.notify('','error')
                    }
                })
                .catch(err=>{
                    this.notify('','error')
                })
            }else{
                this.textarea=''
                this.notify('','warning')
            }
        },
        //
        getComment(){
            getAllComment(this.type,this.playId)
                .then(res => {
                    this.commentList = res;
                    for(let item of res){
                        this.getUsers(item.userId);
                    }
                })
                .catch(err =>{
                    this.notify('','error');
                })
        },
        //
        getUsers(id){
            getUserOfId(id)
            .then(res=>{
                this.commentUser.push(res)
            })
            .catch(err=>{
                this.notify('','error')
            })
        },
        //
        postUp(id,up,index){
            if(this.loginIn){
                let params=new URLSearchParams()
                params.append('id',id)
                params.append('up',up+1)
                addLike(params)
                .then(res=>{
                    if(res.code==1){
                        this.$refs.up[index].children[0].style.color='#2796cd'
                        this.getComment()
                    }else{
                        this.notify('','error')
                    }
                })
                .catch(err=>{
                    this.notify('','error')
                })
            }else{
                this.notify('','warning')
            }
        },
        //
        handleCurrentChange(val){
            this.currentPage=val
        },
    }
}

</script>
<style lang="scss" scoped>
@import '../assets/css/comment.scss'

</style>


