<template>
    <div>
        <div class="info">
            <div class="title">
                <span></span>
            </div>
            <hr/>
            <div class="personal">
                <el-form :model="registerFrom" ref="registerFrom" label-width="70px" class="demo-ruleFrom" :rules="rules">
                    <el-form-item prop="username" label="">
                        <el-input v-model="registerFrom.username" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="password" label="">
                        <el-input type="password" v-model="registerFrom.password" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="sex" label="">
                        <el-radio-group v-model="registerFrom.sex">
                            <el-radio :label="0"></el-radio>
                            <el-radio :label="1"></el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item prop="phone" label="">
                        <el-input  v-model="registerFrom.phone" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="email" label="">
                        <el-input  v-model="registerFrom.email" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="birthday" label="">
                        <el-date-picker type="date" :editable="false" v-model="registerFrom.birthday" placeholder="" style="width: 100%;"></el-date-picker>
                    </el-form-item>
                    <el-form-item prop="introduction" label="">
                        <el-input  v-model="registerFrom.introduction" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item prop="location" label="">
                        <el-select  v-model="registerFrom.location" placeholder="" style="width: 100%;">
                            <el-option v-for="item in cities" :key="item.value" :label="item.label" :value="item.value"></el-option>
                        </el-select>
                    </el-form-item>
                </el-form>
                <div class="btn">
                    <div @click="saveMsg"></div>
                    <div @click="goback(-1)"></div>
                    
                </div>
            </div>
        </div>
    </div>
    
</template>

<script>
import { rules,cities } from '../../assets/data/form'
import {mixin} from '../../mixins'
import {signUp,getUserOfId,updateUserOfuserId} from '../../api/index'
import { mapGetters } from 'vuex'
export default {
    name:'info',
    mixins:[mixin],
    data(){
        return{
            registerFrom:{
                username:'',    //
                password:'',    //
                sex:'',         //
                phone:'',    //
                birthday:'',     //
                introduction:'',    //
                location:'',        //
                email:'',       //
            },
            cities:[],      //--
            rukes:{         //

            },
        }
    },
    created(){
        this.rules=rules
        this.cities=cities
    },
    mounted(){
        this.getUserMsg(this.userId)
    },
    computed:{
        ...mapGetters([
            'userId'
        ])
    },
    methods:{
        //
        getUserMsg(userId){
            getUserOfId(userId)
            .then(res=>{
                this.registerFrom.username=res.username
                this.registerFrom.password=res.password
                this.registerFrom.sex=res.sex
                this.registerFrom.phone=res.phone
                this.registerFrom.birthday=res.birthday
                this.registerFrom.introduction=res.introduction
                this.registerFrom.location=res.location
                this.registerFrom.email=res.email
            })
        },
        //
        saveMsg(){
            let _this=this
            let d=new Date(this.registerFrom.birthday)
            let datetime=d.getFullYear()+'-'+(d.getMonth()+1)+'-'+d.getDate()
            let params =new URLSearchParams()
            params.append('id',this.userId)
            params.append('username',this.registerFrom.username)
            params.append('password',this.registerFrom.password)
            params.append('sex',this.registerFrom.sex)
            params.append('phone',this.registerFrom.phone)
            params.append('email',this.registerFrom.email)
            params.append('birthday',datetime)
            params.append('introduction',this.registerFrom.introduction)
            params.append('location',this.registerFrom.location)
            updateUserOfuserId(params)      //api
            .then(res=>{
                if(res.code==1){
                    _this.$store.commit('setUsername',_this.registerFrom.username)
                    _this.notify('','success')
                    // setTimeout(function(){
                    //     _this.$router.push({path:'/'})
                    //     _this.$store.commit('setActiveName','')
                    // },1000)
                }else{
                    _this.notify('','error')
                }
            })
            .catch(err=>{
                _this.notify('','error');
            })
        },
        goback(index){
            this.$router.go(index)
        },
    }
}
</script>

<style lang="scss" scoped>
@import '../../assets/css/info.scss'
</style>


