<template>
    <div class="song-list-album">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImageUrl(bookContent.pic)">
            </div>
            <div class="album-info">
                <el-button type="danger" style="margin-bottom: 10px;" @click="collect(userId,bookId,0)" v-show="!isCollect"></el-button>
                <el-button type="info" style="margin-bottom: 10px;" @click="delCollect(userId,bookId,0)" v-show="isCollect"></el-button>
                <el-button type="success" style="margin-bottom: 10px;" @click="download"></el-button>
                <el-button type="primary" style="margin-bottom: 10px;" @click="showFile"></el-button>
                <h2>{{ replaceLName(bookContent.name) }}</h2>
                <h2>: {{ replaceFName(bookContent.name) }}</h2>
                <h2></h2>
                <span>
                    {{ bookContent.introduction }}
                </span>
            </div>
        </div>
        <div class="album-content">
            <div class="album-title">
                <p>{{ bookContent.title }}</p>
            </div>
            <div class="album-score">
                <div>
                    <h3></h3>
                    <div>
                        <el-rate v-model="average" disabled></el-rate>
                    </div>
                </div>
                <span>{{ average*2 }}</span>
                <div>
                    <h3></h3>
                    <div @click="setRank">
                        <el-rate v-model="rank" allow-half show-text></el-rate>
                    </div>
                </div>
            </div>
            <div class="songs-body">
                <comment :playId="bookId" :type="0"></comment>
            </div>
        </div>
        <el-dialog
        :visible="show === true"
        :append-to-body='true'
        :modal-append-to-body='true'
        :lock-scroll='true'
        :destroy-on-close='true'
        :close-on-click-modal='false'
        :width="'80%'"
        class="dialog-div-pre-style"
        :before-close="closePreviewClick"
        top="50px"
        center>
            <div v-if="show === true" class="dialog-body-content-base-style"
                style="justify-content: center; align-items: center">
                <embed :src="url" style="width: 100%; height: 600px"/>
            </div>
    </el-dialog>
    </div>
</template>

<script>
import {mixin} from '../mixins'
import {mapGetters} from 'vuex'
import {bookOfBookId,rankAdd,avaBookScore,download} from '../api/index'
import AlbumContent from '../components/AlbumContent.vue'
import Comment from '../components/Comment.vue'
export default{
    name:'book-list-album',
    mixins:[mixin],
    components:{
        AlbumContent,
        Comment,
    },
    data(){
        return{
            bookId:'',     //id
            average:0,          //
            rank:0,             //
            bookContent:{},     //
            url:'',          //url
            show:false,     // 
        }
    },
    computed:{
        ...mapGetters([
            'loginIn',          //
            'userId',           //id
            'isCollect',        //
        ]),
    },
    created(){
        this.bookId=this.$route.params.id   //id
        this.getBook(this.bookId)
        this.getAverage(this.bookId)
        this.haveCollect(this.userId,this.bookId,0)
    },
    watch:{
        bookContent(){
            this.url=this.$store.state.config.HOST+this.bookContent.url
        }
    },
    methods:{
        //id
        getBook(id){
            bookOfBookId(id)
            .then(res=>{
                this.bookContent=res
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //
        getAverage(id){
            avaBookScore(id)
            .then(res=>{
                this.average=res/2
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //
        setRank(){
            if(this.loginIn){
                let params=new URLSearchParams()
                params.append('bookId',this.bookId)
                params.append('userId',this.userId)
                params.append('score',this.rank*2)
                rankAdd(params)
                .then(res=>{
                    if(res.code==1){
                        this.notify('','success')
                        this.getAverage(this.bookId)
                    }else{
                        this.notify('','error')
                    }
                })
                .catch(err=>{
                    this.notify('','error')
                })
            }else{
                this.rank=null
                this.notify('','warning')
            }
        },
        //
        download(){
            download(this.bookContent.url)
            .then(res=>{
                let content=res.data
                let eleLink=document.createElement('a')
                 if(this.bookContent.url.substring(this.bookContent.url.length-3)=='pdf'){
                    eleLink.download=`${this.bookContent.name}.pdf`
                 }else{
                    eleLink.download=`${this.bookContent.name}.txt`
                 }
                // alert(this.bookContent.url.substring(this.bookContent.url.length-3))
                eleLink.style.display='none'
                //blob
                let blob=new Blob([content])
                eleLink.href=URL.createObjectURL(blob)
                //document
                document.body.appendChild(eleLink)
                //
                eleLink.click()
                //
                document.body.removeChild(eleLink)
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //
        showFile(){
            this.show=true
        },
        //
        closePreviewClick(){
            this.show=false
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/song-list-album.scss'
</style>



