<template>
    <div>
        <login-logo/>
        <div class="signUp">
            <div class="signUp-head">
                <span></span>
            </div>
            <el-form :model="registerFrom" ref="registerFrom" label-width="70px" class="demo-ruleFrom" :rules="rules">
                <el-form-item prop="username" label="">
                    <el-input v-model="registerFrom.username" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="password" label="">
                    <el-input type="password" v-model="registerFrom.password" placeholder=""></el-input>
                </el-form-item>
                <div class="login-btn">
                    <el-button @click="goSignUp"></el-button>
                    <el-button type="primary" @click="handleLoginIn"></el-button>
                </div>
            </el-form>
        </div>
    </div>
    
</template>

<script>
import LoginLogo from '../components/LoginLogo'
import {mixin} from '../mixins'
import {loginIn} from '../api/index'
export default {
    components: { LoginLogo },
    name:'login-in',
    mixins:[mixin],
    data(){
        return{
            registerFrom:{
                username:'',    //
                password:'',    //
            },
            rules:{
                username: [
                    { required: true, trigger: 'blur',message:'' }
                ],
                password: [
                    { required: true, trigger: 'blur',message:'' }
                ],
            }
        }
    },
    mounted(){
        this.changeIndex('')
    },
    methods:{
        //
        handleLoginIn(){
            let _this=this
            let params =new URLSearchParams()
            params.append('username',this.registerFrom.username)
            params.append('password',this.registerFrom.password)
            loginIn(params)      //api
            .then(res=>{
                if(res.code==1){
                    _this.notify('','success');
                    _this.$store.commit('setLoginIn',true);
                    _this.$store.commit('setUserId',res.userMsg.id);
                    _this.$store.commit('setUsername',res.userMsg.username);
                    _this.$store.commit('setAvator',res.userMsg.avator);                                                
                    setTimeout(function(){
                        _this.changeIndex('');
                        _this.$router.push({path: '/'});
                    },2000);
                }else{
                    _this.notify('','error')
                }
            })
            .catch(err=>{
                _this.notify('','error');
            })
        },
        //
        goSignUp(){
            this.changeIndex('')
            this.$router.push({path:'/sign-up'})
        },
        //
        changeIndex(value){
            this.$store.commit('setActiveName',value);
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/sign-up.scss'
</style>


