<template>
    <div class="table" style="margin-top: 35px;">
        <div class="container">
            <div class="handle-box">
                <el-button type="danger" size="mini" @click="delAll"></el-button>
                <el-input v-model="select_word" size="mini" placeholder="" class="handle-input"></el-input>
                <el-button type="primary" size="mini" @click="goPublish"></el-button>
            </div>
        </div>
        <el-table size="mini" ref="multipleTable" border style="width:100%" height="700px" :data="data" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="40"></el-table-column>
            <el-table-column label="" width="110" align="center">
                <template slot-scope="scope">
                    <div class="song-img">
                        <img :src="getUrl(scope.row.pic)" style="width:100%"/>
                    </div>
                </template>
            </el-table-column>
            <el-table-column prop="name" label="-" width="120" align="center"></el-table-column>
            <el-table-column label="" class="sidebar">
                <template slot-scope="scope">
                    <p class="testp" >
                        {{ scope.row.introduction }}
                    </p>
                </template>
            </el-table-column>
            <el-table-column label="" width="80" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="getContent(scope.row.id)"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" align="center" width="100">
                <template slot-scope="scope">
                    <el-upload :action="uploadUrl(scope.row.id)" :before-upload="beforeAvatorUpload" 
                    :on-success="handleAvatorSuccess">
                        <el-button size="mini"></el-button>
                    </el-upload>
                    <br>
                    <el-upload :action="uploadBookUrl(scope.row.id)" :before-upload="beforeBookUpload"
                        :on-success="handleBookSuccess">
                        <el-button size="mini"></el-button>
                    </el-upload>
                </template>
            </el-table-column>
            <el-table-column label="" width="150" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                background
                layout="total,prev,pager,next"
                :current-page="currentPage"
                :page-size="pageSize"
                :total="tableData.length"
                @current-change="handleCurrentChange"
            >
            </el-pagination>
        </div>
<!--  -->
        <el-dialog title="" :visible.sync="editVisible" width="400px" center>
            <el-form :model="form" ref="from" label-width="80px">
                <el-form-item prop="name" label="-" size="mini">
                    <el-input v-model="form.name" placeholder="-"></el-input>
                </el-form-item>
                <el-form-item prop="introduction" label="" size="mini">
                    <el-input v-model="form.introduction" placeholder="" type="textarea"></el-input>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="editVisible = false"></el-button>
                <el-button size="mini" @click="editSave"></el-button>
            </span>
        </el-dialog>
        <el-dialog title="" :visible.sync="delVisible" width="300px" center>
            <div align="center">,</div>
            <span slot="footer">
                <el-button size="mini" @click="delVisible = false"></el-button>
                <el-button size="mini" @click="delRow"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import {bookOfUserId,updateBook,delBook} from '../../api/index'
import { mixin } from '../../mixins'
import  '@/assets/js/iconfont.js'
import { mapGetters } from 'vuex'
export default{
    mixins:[mixin],
    data(){
        return{
            authorId:'',//id
            authorName:'',//
            delVisible:false,   //
            registerForm:{  //
                name:'',
                authorName:'',
                introduction:'',
            },
            editVisible:false,   //
            form:{  //
                id:'',
                name:'',
                introduction:'',
            },
            tableData:[],
            tempData:[],
            select_word:'',
            pageSize:5, //
            currentPage:1,
            idx:-1,      //
            multipleSelection:[],   //
            toggle:false,       //
        }
    },
    computed:{
        ...mapGetters([
            'loginIn',
            'userId'
        ]),
        //
        data(){
            return this.tableData.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize)
        }
    },
    watch:{
        //
        select_word:function(){
            if(this.select_word==''){
                this.tableData=this.tempData
            }else{
                this.tableData=[]
                for(let item of this.tempData){
                    if(item.name.includes(this.select_word)){
                        this.tableData.push(item)
                    }
                }
            }
        }
    },
    created(){
        this.authorId=this.$route.query.id
        this.authorName=this.$route.query.name
        this.getData()
    },
    destroyed(){
    },
    methods:{
        //
        getContent(id){
            this.$router.push({path:`/book-album/${id}`})
        },
        //Url
        uploadBookUrl(id){
            return `${this.$store.state.config.HOST}/book/updateBookUrl?id=${id}`
        },
        //
        beforeBookUpload(file){
            var testMsg=file.name.substring(file.name.lastIndexOf('.')+1)
            if(testMsg!='pdf'&&testMsg!='txt'){
                this.$message({
                    message:'txtpdf',
                    type:'error'
                })
                return false
            }
            return true
        },
        //
        handleBookSuccess(res){
            let _this=this
            if(res.code== 1){
                _this.getData()
                _this.$notify({
                    title:'',
                    type:'success'
                })
            }else{
                _this.$notify({
                    title:'',
                    type:'error'
                })
            }
        },
        //
        delRow(){
            delBook(this.idx)
            .then(res=>{
                if(res){
                    this.getData()
                    this.notify('','success')
                }else{
                    this.notify('','error')
                }
            })
            .catch(err => {
                console.log(err)
            })
            this.delVisible=false
        },
        //
        editSave(){
            let params=new URLSearchParams()
            params.append('id',this.form.id)
            params.append('name',this.form.name)
            params.append('introduction',this.form.introduction)
            updateBook(params)
            .then(res=>{
                if(res.code == 1){
                    this.getData()
                    this.notify('','success')
                }else{
                    this.notify('','error')
                }
            })
            .catch(err => {
                console.log(err)
            })
            this.editVisible=false
        },
        //
        handleEdit(row){
            this.editVisible=true
            this.form={
                id:row.id,
                name:row.name,
                introduction:row.introduction,
                lyric:row.lyric
            }
        },
        //
        handleCurrentChange(val){
            this.currentPage=val
        },
        //
        uploadUrl(id){
            return `${this.$store.state.config.HOST}/book/updateBookPic?id=${id}`
        },
        //
        getData(){
            this.tableData=[]
            this.tempData=[]
            bookOfUserId(this.userId).then(res=>{
                this.tableData=res
                this.tempData=res
                this.currentPage=1
            })
        },
        //
        goPublish(){
            this.$router.push({path:'/publish'})
        }
    }
}
</script>

<style scoped>
.handle-box{
    margin-bottom: 20px;
}
.song-img{
    width: 100%;
    height: 80px;
    border-radius: 5px;
    margin-bottom: 5px;
    overflow: hidden;
}
.handle-input{
    width: 300px;
    display: inline-block;
}
.pagination{
    display: flex;
    justify-content: center;
}
.play{
    position: absolute;
    z-index: 100;
    width: 80px;
    height:80px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    top: 18px;
    left:15px;
}
.icon{
    width: 2em;
    height: 2em;
    color: white;
    fill: currentColor;
    overflow: hidden;
}
.text-display{
    text-overflow: ellipsis;
}
.testp{
    width: 100%;
overflow : hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;
}
</style>



