<template>
    <div class="table">
        <div class="container">
            <div class="handle-box">
                <el-button type="danger" size="mini" @click="delAll"></el-button>
                <el-input v-model="select_word" size="mini" placeholder="" class="handle-input"></el-input>
                <el-button type="primary" size="mini" @click="goPublish"></el-button>
            </div>
        </div>
        <el-table size="mini" ref="multipleTable" border style="width:100%" height="700px" :data="data" @selection-change="handleSelectionChange">
            <el-table-column type="selection" width="40"></el-table-column>
            <el-table-column label="" width="110" align="center">
                <template slot-scope="scope">
                    <div class="songList-img">
                        <img :src="getUrl(scope.row.pic)" style="width:100%"/>
                    </div>
                    <el-upload :action="uploadUrl(scope.row.id)" :before-upload="beforeAvatorUpload" 
                    :on-success="handleAvatorSuccess">
                        <el-button size="mini"></el-button>
                    </el-upload>
                </template>
            </el-table-column>
            <el-table-column prop="title" label="" width="120" align="center"></el-table-column>
            <el-table-column label="" class="sidebar">
                <template slot-scope="scope">
                    <p class="testp" >
                        {{ scope.row.introduction }}
                    </p>
                </template>
            </el-table-column>
            <el-table-column prop="style" label="" width="120" align="center"></el-table-column>
            <el-table-column label="" width="110" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="listBookMan(scope.row.id)"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" width="80" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="getContent(scope.row.id)"></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" width="150" align="center">
                <template slot-scope="scope">
                    <el-button size="mini" @click="handleEdit(scope.row)"></el-button>
                    <el-button size="mini" type="danger" @click="handleDelete(scope.row.id)"></el-button>
                </template>
            </el-table-column>
        </el-table>
        <div class="pagination">
            <el-pagination
                background
                layout="total,prev,pager,next"
                :current-page="currentPage"
                :page-size="pageSize"
                :total="tableData.length"
                @current-change="handleCurrentChange"
            >
            </el-pagination>
        </div>
<!--  -->
        <el-dialog title="" :visible.sync="manVisible" width="700px" center>
            <div class="container">
                <div class="handle-box">
                    <el-button type="danger" size="mini" @click="delManAll"></el-button>
                    <el-button type="primary" size="mini" @click="getAllBook"></el-button>
                </div>
            </div>
            <el-table size="mini" ref="multipleTable" border style="width:100%" height="400px" :data="manData" @selection-change="handleSelection">
                <el-table-column type="selection" width="40"></el-table-column>
                <el-table-column prop="id" label="id" align="center"></el-table-column>
                <el-table-column prop="name" label="-" align="center"></el-table-column>
                <el-table-column label="" width="80" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="getBookContent(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
                <el-table-column label="" width="150" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" type="danger" @click="delListBook(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination" style="margin-bottom: 15px;">
                <el-pagination @current-change="handleManDataCurrentChange" background layout="total,prev,pager,next"
                    :current-page="currentManPage" :page-size="pageSize" :total="listBookDate.length"></el-pagination>
            </div>
        </el-dialog>
<!--  -->
        <el-dialog title="" :visible.sync="addVisible" width="700px" center>
            <div class="container">
                <div class="handle-box">
                    <el-button type="danger" size="mini" @click="addManAll"></el-button>
                </div>
            </div>
            <el-table size="mini" ref="multipleTable" border style="width:100%" height="400px" :data="addData" @selection-change="handleAddSelection">
                <el-table-column type="selection" width="40"></el-table-column>
                <el-table-column prop="id" label="id" align="center"></el-table-column>
                <el-table-column prop="name" label="-" align="center"></el-table-column>
                <el-table-column label="" width="80" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" @click="getBookContent(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
                <el-table-column label="" width="150" align="center">
                    <template slot-scope="scope">
                        <el-button size="mini" type="danger" @click="addListBook(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
            <div class="pagination" style="margin-bottom: 15px;">
                <el-pagination @current-change="handleAddDataCurrentChange" background layout="total,prev,pager,next"
                    :current-page="currentAddPage" :page-size="pageSize" :total="allBook.length"></el-pagination>
            </div>
        </el-dialog>
<!--  -->
        <el-dialog title="" :visible.sync="editVisible" width="400px" center>
            <el-form :model="form" ref="form" label-width="80px">
                <el-form-item prop="title" label="" size="mini">
                    <el-input v-model="form.title" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="introduction" label="" size="mini">
                    <el-input v-model="form.introduction" placeholder="" type="textarea"></el-input>
                </el-form-item>
                <el-form-item prop="style" label="">
                    <el-select  v-model="form.style" placeholder="" style="width: 100%;">
                        <el-option v-for="item in styles" :key="item.name" :label="item.name" :value="item.name"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer">
                <el-button size="mini" @click="editVisible = false"></el-button>
                <el-button size="mini" @click="editSave"></el-button>
            </span>
        </el-dialog>
        <el-dialog title="" :visible.sync="delVisible" width="300px" center>
            <div align="center">,</div>
            <span slot="footer">
                <el-button size="mini" @click="delVisible = false"></el-button>
                <el-button size="mini" @click="delRow"></el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import { mixin } from '../../mixins'
import { mapGetters } from 'vuex'
import { styles } from '../../assets/data/style'
import { getBookListByUserId,updateBookList,getBookListById,delBookList,bookOfBookId,listBookDetail,delListBook,getAllBook,listBookAdd } from '../../api'
export default{
    name:'book-list-manage',
    mixins:[mixin],
    data(){
        return{
            manVisible:false,   //
            delVisible:false,   //
            registerForm:{  //
                title:'',
                introduction:'',
                style:''
            },
            editVisible:false,   //
            form:{  //
                id:'',
                title:'',
                introduction:'',
                style:''
            },
            tableData:[],
            tempData:[],
            select_word:'',
            pageSize:5, //
            currentPage:1,
            currentAddPage:1,
            currentManPage:1,
            idx:-1,      //
            multipleSelection:[],    //
            styles:[],
            listBookDate:[],        //
            delSelection:[],    //
            manageListId:'',    //id
            allBook:[],         //
            addSelection:[],    //
            addVisible:false,   //
        }
    },
    computed:{
        //
        data(){
            return this.tableData.slice((this.currentPage-1)*this.pageSize,this.currentPage*this.pageSize)
        },
        addData(){
            return this.allBook.slice((this.currentAddPage-1)*this.pageSize,this.currentAddPage*this.pageSize)
        },
        manData(){
            return this.listBookDate.slice((this.currentManPage-1)*this.pageSize,this.currentManPage*this.pageSize)
        },
        ...mapGetters([
            'loginIn',
            'userId',
            'tempList'
        ])
    },
    watch:{
        //
        select_word:function(){
            if(this.select_word==''){
                this.tableData=this.tempData
            }else{
                this.tableData=[]
                for(let item of this.tempData){
                    if(item.title.includes(this.select_word)){
                        this.tableData.push(item)
                    }
                }
            }
        }
    },
    created(){
        this.getData()
        this.styles=styles
    },
    methods:{
        //
        delRow(){
            delBookList(this.idx)
            .then(res=>{
                if(res){
                    this.getData()
                    this.notify('','success')
                }else{
                    this.notify('','error')
                }
            })
            .catch(err => {
                console.log(err)
            })
            this.delVisible=false
        },
        //
        editSave(){
            let params=new URLSearchParams()
            params.append('id',this.form.id)
            params.append('title',this.form.title)
            params.append('introduction',this.form.introduction)
            params.append('style',this.form.style)
            updateBookList(params)
            .then(res=>{
                if(res.code == 1){
                    this.getData()
                    this.notify('','success')
                }else{
                    this.notify('','error')
                }
            })
            .catch(err => {
                console.log(err)
            })
            this.editVisible=false
        },
        //
        handleEdit(row){
            this.editVisible=true
            this.form={
                id:row.id,
                title:row.title,
                introduction:row.introduction,
                style:row.style
            }
        },
        //
        handleCurrentChange(val){
            this.currentPage=val
        },
        handleManDataCurrentChange(val){
            this.currentManPage=val
        },
        handleAddDataCurrentChange(val){
            this.currentAddPage=val
        },
        //
        uploadUrl(id){
            return `${this.$store.state.config.HOST}/bookList/updateBookListPic?id=${id}`
        },
        //
        getData(){
            this.tableData=[]
            this.tempData=[]
            getBookListByUserId(this.userId).then(res=>{
                this.tableData=res
                this.tempData=res
                this.currentPage=1
            })
        },
        //,templist
        getContent(id){
            getBookListById(id).then(res=>{
                let bookList=res
                this.$store.commit('setTempList',bookList)
            })
            this.$router.push({path:`book-list-album/${id}`})
        },
        //
        goPublish(){
            this.$router.push({path:'/publish'})
        },
        listBookMan(id){
            this.manVisible=true
            this.listBookDate=[]
            listBookDetail(id).then(res=>{
                for(let item of res){
                    this.getBook(item.bookId)
                }
            })
            this.manageListId=id
        },
        /***** */
        //
        getBook(id){
            bookOfBookId(id)
            .then(res=>{
                this.listBookDate.push(res)
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //
        getBookContent(id){
            this.$router.push({path:`/book-album/${id}`})
        },
        //
        delListBook(id){
            delListBook(id,this.manageListId)
            .then(res=>{
                if(res){
                    this.listBookMan(this.manageListId)
                    this.notify('','success')
                }else{
                    this.notify('','error')
                }
            })
            .catch(err => {
                console.log(err)
            })
        },
        handleSelection(val){
            this.delSelection=val
        },
        delManAll(){
            for(let item of this.delSelection){
                this.delListBook(item.id)
            }
            this.delSelection=[]
            //this.$router.go(0)
        },
        /***** */
        getAllBook(){
            this.addVisible = true
            this.delVisible=false
            this.allBook=[]
            getAllBook()
            .then(res=>{
                this.allBook=res
            })
        },
        addManAll(){
            for(let item of this.addSelection){
                this.addListBook(item.id)
            }
            this.addSelection=[]
            this.$router.go(0)
        },
        handleAddSelection(val){
            this.addSelection=val
        },
        addListBook(id){
            let params=new URLSearchParams()
            params.append('bookId',id)
            params.append('bookListId',this.manageListId)
            listBookAdd(params)
            .then(res=>{
                if(res.code==1){
                    this.listBookMan(this.manageListId)
                    this.notify('','success')
                }else{
                    this.notify('','error')
                }
            })
            .catch(err=>{
                console.log(err)
            })
            
        },
    }
}
</script>

<style scoped>
.handle-box{
    margin-bottom: 20px;
}
.songList-img{
    width: 100%;
    height: 80px;
    border-radius: 5px;
    margin-bottom: 5px;
    overflow: hidden;
}
.handle-input{
    width: 300px;
    display: inline-block;
}
.pagination{
    display: flex;
    justify-content: center;
}
.testp{
    width: 100%;
overflow : hidden;
text-overflow: ellipsis;
display: -webkit-box;
-webkit-line-clamp: 3;
-webkit-box-orient: vertical;
}

</style>



