<template>
    <div class="song-list-album">
        <div class="album-slide">
            <div class="album-img">
                <img :src="attachImageUrl(tempList.pic)">
            </div>
            <div class="album-info">
                <el-button type="danger" style="margin-bottom: 10px;" @click="collect(userId,bookListId,1)" v-show="!isCollect"></el-button>
                <el-button type="info" style="margin-bottom: 10px;" @click="delCollect(userId,bookListId,1)" v-show="isCollect"></el-button>
                <h2></h2>
                <span>
                    {{ tempList.introduction }}
                </span>
            </div>
        </div>
        <div class="album-content">
            <div class="album-title">
                <p>{{ tempList.title }}</p>
            </div>
            <div class="album-score">
                <div>
                    <h3></h3>
                    <div>
                        <el-rate v-model="average" disabled></el-rate>
                    </div>
                </div>
                <span>{{ average*2 }}</span>
                <div>
                    <h3></h3>
                    <div @click="setRank">
                        <el-rate v-model="rank" allow-half show-text></el-rate>
                    </div>
                </div>
            </div>
            <div class="songs-body">
                <album-content :bookList="listOfBooks">
                    <template slot="title"></template>
                </album-content>
                <comment :playId="bookListId" :type="1"></comment>
            </div>
        </div>
    </div>
</template>

<script>
import {mixin} from '../mixins'
import {mapGetters} from 'vuex'
import {listBookDetail,bookOfBookId,rankAdd,avaScore} from '../api/index'
import AlbumContent from '../components/AlbumContent.vue'
import Comment from '../components/Comment.vue'
export default{
    name:'book-list-album',
    mixins:[mixin],
    components:{
        AlbumContent,
        Comment,
    },
    data(){
        return{
            bookLists:[],       //
            bookListId:'',     //id
            average:0,          //
            rank:0,             //
        }
    },
    computed:{
        ...mapGetters([
            'listOfBooks',      //
            'tempList',         //
            'loginIn',          //
            'userId',           //id
            'isCollect',        //
        ]),
    },
    created(){
        this.bookListId=this.$route.params.id   //id
        this.getBookId()
        this.getAverage(this.bookListId)
        this.haveCollect(this.userId,this.bookListId,1)
    },
    methods:{
        //
        getBookId(){
            listBookDetail(this.bookListId)
            .then(res=>{
                for(let item of res){
                    this.getBookList(item.bookId)
                }
                this.$store.commit('setListOfBooks',this.bookLists)
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //id
        getBookList(id){
            bookOfBookId(id)
            .then(res=>{
                this.bookLists.push(res)
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //
        getAverage(id){
            avaScore(id)
            .then(res=>{
                this.average=res/2
            })
            .catch(err=>{
                console.log(err)
            })
        },
        //
        setRank(){
            if(this.loginIn){
                let params=new URLSearchParams()
                params.append('bookListId',this.bookListId)
                params.append('userId',this.userId)
                params.append('score',this.rank*2)
                rankAdd(params)
                .then(res=>{
                    if(res.code==1){
                        this.notify('','success')
                        this.getAverage(this.bookListId)
                    }else{
                        this.notify('','error')
                    }
                })
                .catch(err=>{
                    this.notify('','error')
                })
            }else{
                this.rank=null
                this.notify('','warning')
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/css/song-list-album.scss'
</style>



