import { addCollect, bookLikeName,collectOfUserId,existCollect,delCollect } from "../api/index"
import {mapGetters} from 'vuex';
export const mixin={
    computed:{
        ...mapGetters([
            'loginIn',
            'userId',
            'isCollect',
            'id',
            'HOST'
        ])
    },
    methods:{
        //
        notify(title,type) {
            this.$notify({
                title: title,
                type: type
            })
        },
        //
        attachImageUrl(srcUrl){
            return srcUrl?this.$store.state.config.HOST+srcUrl:this.$store.state.config.HOST+'/img/authorPic/hhh.jpg'
        },
        //
        getSearch(){
            if(!this.$route.query.keywords){
                this.$store.commit('setListOfBooks',[])
                this.notify('','warning')
            }else{
                bookLikeName(this.$route.query.keywords).then(res=>{
                    if(!res.length){
                        this.$store.commit('setListOfBooks',[])
                        this.notify('','warning')
                    }else{
                        this.$store.commit('setListOfBooks',res)
                    }
                }).catch(err=>{
                    console.log(err)
                })
            }
        },
        //
        replaceFName(str){
            let arr=str.split('-')
            return arr[0]
        },
        //
        replaceLName(str){
            let arr=str.split('-')
            return arr[1]
        },
        //
        toplay:function(id,url,pic,index,name,lyric){
            this.$store.commit('setId',id)
            this.$store.commit('setUrl',this.$store.state.config.HOST+url)
            this.$store.commit('setPicUrl',this.$store.state.config.HOST+pic)
            this.$store.commit('setListIndex',index)
            this.$store.commit('setTitle',this.replaceLName(name))
            this.$store.commit('setArtist',this.replaceFName(name))
            this.$store.commit('setLyric',this.parseLyric(lyric))
            this.$store.commit('setIsCollect',false)
            if(this.loginIn){
                collectOfUserId(this.userId)
                .then(res=>{
                    for(let item of res){
                        if(item.songId==id){
                            this.$store.commit('setIsCollect',true)
                            break
                        }
                    }
                })
            }
        },
        //
        parseLyric(text){
            let lines=text.split("\n")      //
            let pattern =/\[\d{2}:\d{2}.(\d{3}|\d{2})\]/g   //
            let result=[]       //
            //
            if(!(/\[.+\]/.test(text))){
                return [[0,text]]
            }
            //
            while(!pattern.test(lines[0])){
                lines=lines.slice(1)
            }
            //,
            for(let item of lines){
                //
                let time=item.match(pattern)
                //
                let value=item.replace(pattern,'')//[00:00:00]
                for(let item1 of time){
                    let t=item1.slice(1,-1).split(":")  //,+
                    if(value!=''){
                        result.push([parseInt(t[0],10)*60+parseFloat(t[1]),value])          //
                    }
                }
            }
            //----
            result.sort(function(a,b){
                return a[0]-b[0]
            })
            return result
        },
        //
        attachBirth(val){
            return val.substr(0,10);
        },
        //
        collect(userId,id,type){
            if(this.loginIn){
                var params=new URLSearchParams()
                if(type==0){//
                    params.append('bookId',id)
                    params.append('type',0)
                }else if(type==1){//
                    params.append('bookListId',id)
                    params.append('type',1)
                }else{
                    params.append('authorId',id)
                    params.append('type',2)
                }
                params.append('userId',userId)
                addCollect(params)
                .then(res=>{
                    if(res.code==1){
                        this.notify('','success')
                        this.haveCollect(userId,id,type)
                    }else if(res.code==2){
                        this.notify('','warning')
                    }else{
                        this.notify('','error')
                    }
                })
            }else{
                this.notify('','warning')
            }
        },
        //
        haveCollect(userId,id,type){
            if(this.loginIn){
                var params=new URLSearchParams()
                if(type==0){//
                    params.append('bookId',id)
                    params.append('type',0)
                }else if(type==1){//
                    params.append('bookListId',id)
                    params.append('type',1)
                }else{
                    params.append('authorId',id)
                    params.append('type',2)
                }
                params.append('userId',userId)
                existCollect(params)
                .then(res=>{
                    if(res.code==1){
                        this.$store.commit('setIsCollect',true)
                    }else{
                        this.$store.commit('setIsCollect',false)
                    }
                })
            }
        },
        //
        delCollect(userId,id,type){
            if(this.loginIn){
                var params=new URLSearchParams()
                if(type==0){//
                    params.append('bookId',id)
                    params.append('type',0)
                }else if(type==1){//
                    params.append('bookListId',id)
                    params.append('type',1)
                }else{
                    params.append('authorId',id)
                    params.append('type',2)
                }
                params.append('userId',userId)
                delCollect(params)
                .then(res=>{
                    if(res.code==1){
                        this.notify('','success')
                        this.haveCollect(userId,id,type)
                    }else{
                        this.notify('','error')
                    }
                })
            }
        },
        //
        delAll(){
            for(let item of this.multipleSelection){
                this.handleDelete(item.id)
                this.delRow()
            }
            this.multipleSelection=[]
        },
        //multipleSelection
        handleSelectionChange(val){
            this.multipleSelection=val
        },
        //
        getUrl(url){
            return `${this.$store.state.config.HOST}${url}`
        },
         //
         beforeAvatorUpload(file){
            const isJPG=(file.type === 'image/jpeg')||(file.type === 'image/png')
            if(!isJPG){
                this.$message.error('jpgpng')
                return false
            }
            const isLt2M=(file.size/1024/1024) < 2
            if(!isLt2M){
                this.$message.error('2M')
                return false
            }
            return true
        },
        //
        handleAvatorSuccess(res){
            let _this=this
            if(res.code== 1){
                _this.getData()
                _this.$notify({
                    title:'',
                    type:'success'
                })
            }else{
                _this.$notify({
                    title:'',
                    type:'error'
                })
            }
        },
        //
        handleDelete(id){
            this.idx=id
            this.delVisible=true
        },
    },
    
}


