<template>
    <div class="upload">
        <p class="title"></p>
        <hr/>
        <div class="section">
            <el-upload drag :action="uploadUrl()" :show-file-list="false" :on-success="handleAvatorSuccess"
                :before-upload="beforeAvatorUpload">
                <i class="el-icon-upload"></i>
                <div>
                    <span style="color:aquamarine"></span>
                </div>
                <div slot="tip" style="text-align: center;">jpg/png10MB</div>
            </el-upload>
        </div>
    </div>
</template>
<script>
import {mapGetters} from 'vuex'
import {mixin} from '../../mixins'
export default{
    name:'upload',
    mixins:[mixin],
    computed:{
        ...mapGetters([
            'userId',
        ])
    },
    methods:{
        //
        uploadUrl(){
            return `${this.$store.state.config.HOST}/user/updateUserPic?id=${this.userId}`
        },
        //
        handleAvatorSuccess(res,file){
            if(res.code==1){
                this.$store.commit('setAvator',res.avator)
                this.notify('','success')
            }else{
                this.notify('','error')
            }
        },
        //
        beforeAvatorUpload(file){
            const isJPG=file.type=='image/jpeg'
            const isLt10M=(file.size/1024/1024)<10
            if(!isJPG){
                this.notify('jpg','error')
            }
            if(!isLt10M){
                this.notify('10M','error')
                return false
            }
            return true
        }
    }
}
</script>
<style lang="scss" scoped>
@import '../../assets/css/upload.scss'
</style>


